import "./Main.css"
import { useLocation } from "react-router-dom"
import { useState , useEffect , lazy, Suspense } from "react"

const First_content = lazy(() => import( "./First_content/First_content.js"))
const Second_content = lazy(() => import("./Second_content/Second_content.js"))

const Main = () => {

    let [mainClass , setMainClass] = useState("hide-main")
    let [component , setComponent] = useState(<First_content/>)
    let [width , setWidth] = useState("1080")

    let location = useLocation()

    setInterval(() => {

        setWidth(window.innerWidth)
        
    },);

    useEffect(()=>{

        if(width <= 880){
            setComponent(<Second_content/>)
        }else if(width >= 881){
            setComponent(<First_content/>)
        }

    },[width])

    useEffect(()=>{

        if(location.pathname === "/produce") {

            setMainClass("show-main")

        }else{
            setMainClass("hide-main")
        }

    },[location.pathname])



    return (

        <main className={mainClass}>

            <Suspense fallback={<p>Loding Component...!</p>}>

                {component}
                
            </Suspense>

        </main>
        
    )
}

export default Main